import React from "react";

function ViolationDetection() {
  const handleSubmit = (e) => {
    e.preventDefault();
    // Code to detect violations
    alert("Violation detected and reported.");
  };

  return (
    <div className="container">
      <h2>Violation Detection</h2>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <label className="form-label">Upload Video/Image</label>
          <input type="file" className="form-control" accept="video/*, image/*" />
        </div>
        <button type="submit" className="btn btn-warning">
          Submit
        </button>
      </form>
    </div>
  );
}

export default ViolationDetection;
