import React from 'react';
import { useState } from 'react';
import './AccidentDetection.css';  // custom CSS and animations

const AccidentDetection = () => {
  const [videoInput, setVideoInput] = useState(null);

  const handleVideoUpload = (e) => {
    setVideoInput(e.target.files[0]);
  };

  const handleSubmit = async () => {
    const formData = new FormData();
    formData.append('video', videoInput);
    
    const response = await fetch('/detect-accident', {
      method: 'POST',
      body: formData,
    });

    const result = await response.json();
    alert(result.message);
  };

  return (
    <div className="container accident-page">
      <h1>Accident Detection and Reporting</h1>
      <p>Upload video to detect accidents and notify authorities automatically.</p>
      <input type="file" accept="video/*" onChange={handleVideoUpload} className="form-control" />
      <button onClick={handleSubmit} className="btn btn-primary mt-3">Submit</button>
    </div>
  );
};

export default AccidentDetection;
