import React from 'react';
import { Link } from 'react-router-dom';

const Dashboard = () => {
  return (
    <div className="container">
      <h1>Smart Dashboard for Authorities</h1>
      <div className="row">
        <div className="col-md-4">
          <Link to="/accident-detection" className="btn btn-outline-primary">Accident Detection</Link>
        </div>
        <div className="col-md-4">
          <Link to="/traffic-congestion" className="btn btn-outline-secondary">Traffic Congestion</Link>
        </div>
        <div className="col-md-4">
          <Link to="/violation-detection" className="btn btn-outline-danger">Violation Detection</Link>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
